.container-5 {
    background: #0000;
    width: 80rem;
    height: 50rem;
    max-width: 80rem;
    padding: 2.5rem 4rem 1rem 4rem;
    box-shadow: 0rem 0rem 1rem rgba(0, 0, 0, 0.9);
    border-radius: 5rem 5rem 5rem 5rem;
}

.container-5 .text {
    font-size: 4.5rem;
    font-weight: 600;
    text-align: center;
    background: -webkit-linear-gradient(right, #272c2c, #7e5b8f, #1c3436, #201824);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Abel', sans-serif;
}

.container-5 form {
    padding: 3rem 0 0 0;
}

.container-5 form .form-row {
    display: -webkit-flex;
    display: flex;
    margin: 3.2rem 0;
}

form .form-row .input-data {
    width: 100%;
    height: 4rem;
    margin: 0 2rem;
    position: relative;
}

form .form-row .textarea {
    height: 7rem;
}

.input-data input,
.textarea textarea {
    display: block;
    height: 100%;
    width: 100%;
    border: none;
    outline: none;
    font-size: 1.7rem;
    border-bottom: .2rem solid rgba(0, 0, 0, 0.12);
    background: transparent;
}

.input-data label {
    position: absolute;
    bottom: 2rem;
    font-size: 1.6rem;
    pointer-events: none;
    transition: all 0.3s ease;
    font-family: 'Abel', sans-serif;
    font-weight: bold;
}

.input-data .underline {
    position: absolute;
    bottom: 0;
    height: 2px;
    width: 100%
}

.textarea label {
    width: 100%;
    bottom: 3rem;
    background: transparent;
}

.textarea textarea {
    resize: none;
    padding-top: 2rem;
}

.input-data input:focus~label,
.textarea textarea:focus~label,
.input-data input:valid~label,
.textarea textarea:valid~label {
    -webkit-transform: translateY(-2rem);
            transform: translateY(-2rem);
    color: rgb(88, 29, 29);
    font-size: 1.6rem;
    border: none;
}

.input-data .underline:before {
    position: absolute;
    content: "";
    height: .2rem;
    width: 100%;
    background: #36929b;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: center;
            transform-origin: center;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.input-data input:focus~.underline:before,
.input-data input:valid~.underline:before,
.textarea textarea:focus~.underline:before,
.textarea textarea:valid~.underline:before {
    -webkit-transform: scale(1);
            transform: scale(1);
}

