    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    border-radius: 1rem;
    transition: .5s;
}

.pic::before {
    content: "Photo Gallery";
    left: 50%;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    font-size: 2rem;
    font-weight: bold;
    margin-top: -10rem;
    opacity: 0;
    transition: .5s;
    transition-delay: .2s;
    z-index: 1;
    font-family: 'Architects Daughter', cursive;
