.pic::after {
    content: "";
    width: 100%;
    height: 0;
    left: 0;
    bottom: 0;
    position: absolute;
    border-radius: 1rem;
    background-color: rgba(0, 0, 0, 0.4);
    transition: .3s;
}

.pic:hover::after {
    height: 100%;
}

.pic:hover::before {
    margin-top: 0;
    opacity: 1;
}

